import { FetchShopify, ShopifyCart, CartProviderConfig, CartLine } from "@t";
import {
  normalizeMetafields,
  buildMetafieldIdentifiers,
  castMetafields,
  camelizeMetafields,
  safeExtract,
} from "@utils";
import { getCartQuery } from "@gql";

export async function getCart(
  fetchShopify: FetchShopify,
  cartId: string,
  config: CartProviderConfig = {}
): Promise<ShopifyCart> {
  const {
    productMetafields = [],
    variantMetafields = [],
    options = {},
  } = config;

  const {
    camelizeKeys = true,
    renderRichTextAsHtml = false,
    resolveFiles = false,
    transformProductMetafields,
    transformVariantMetafields,
  } = options;

  const productMetafieldIdentifiers = productMetafields.length
    ? buildMetafieldIdentifiers(productMetafields)
    : "";

  const variantMetafieldIdentifiers = variantMetafields.length
    ? buildMetafieldIdentifiers(variantMetafields)
    : "";

  const query = getCartQuery(
    productMetafieldIdentifiers,
    variantMetafieldIdentifiers
  );
  const response = await fetchShopify(query, { cartId });

  const rawCart = safeExtract("getCart", response?.data?.cart, {
    cartId,
    response,
    config,
  });

  const rawLines = rawCart?.lines?.edges ?? [];

  const resolvedLines: CartLine[] = await Promise.all(
    rawLines.map(async ({ node }: any): Promise<CartLine> => {
      const variant = node.merchandise;

      // --- Variant Metafields ---
      const rawVariant = normalizeMetafields(
        variant.metafields || [],
        variantMetafields
      );

      const castedVariant = await castMetafields(
        rawVariant,
        variantMetafields,
        renderRichTextAsHtml,
        transformVariantMetafields,
        resolveFiles,
        fetchShopify
      );

      const finalVariantMetafields = camelizeKeys
        ? camelizeMetafields(castedVariant)
        : castedVariant;

      // --- Product Metafields ---
      const product = variant.product;
      const rawProduct = normalizeMetafields(
        product.metafields || [],
        productMetafields
      );

      const castedProduct = await castMetafields(
        rawProduct,
        productMetafields,
        renderRichTextAsHtml,
        transformProductMetafields,
        resolveFiles,
        fetchShopify
      );

      const finalProductMetafields = camelizeKeys
        ? camelizeMetafields(castedProduct)
        : castedProduct;

      return {
        ...node,
        merchandise: {
          ...variant,
          metafields: finalVariantMetafields,
          product: {
            ...variant.product,
            metafields: finalProductMetafields,
          },
        },
      };
    })
  );

  return {
    ...rawCart,
    lines: resolvedLines,
  };
}
