import { FetchShopify, ShopifyCart, CartProviderConfig } from "@t";
import { getCartQuery, removeFromCartMutation } from "@gql";
import { safeExtract } from "@utils";
import { buildMetafieldIdentifiers } from "@utils";

export async function emptyCart(
  fetchShopify: FetchShopify,
  cartId: string,
  config: CartProviderConfig = {}
): Promise<ShopifyCart> {
  const { productMetafields = [], variantMetafields = [] } = config;

  const productMetafieldIdentifiers = productMetafields.length
    ? buildMetafieldIdentifiers(productMetafields)
    : "";

  const variantMetafieldIdentifiers = variantMetafields.length
    ? buildMetafieldIdentifiers(variantMetafields)
    : "";

  const query = getCartQuery(
    productMetafieldIdentifiers,
    variantMetafieldIdentifiers
  );

  const cartResponse = await fetchShopify(query, { cartId });

  const cart = safeExtract("emptyCart (fetch)", cartResponse?.data?.cart, {
    cartId,
    cartResponse,
  });

  const lines = Array.isArray(cart.lines)
    ? cart.lines
    : cart.lines?.edges?.map((edge: { node: any }) => edge.node) || [];

  const lineIds = lines.map((line: { id: string }) => line.id);

  if (lineIds.length === 0) {
    if (process.env.NODE_ENV === "development") {
      console.debug("[emptyCart] Cart already empty");
    }
    return cart;
  }

  const removeResponse = await fetchShopify(removeFromCartMutation, {
    cartId,
    lineIds,
  });

  return safeExtract(
    "emptyCart (remove)",
    removeResponse?.data?.cartLinesRemove?.cart,
    {
      cartId,
      lineIds,
      removeResponse,
    }
  );
}
