import { FetchShopify, ShopifyCart, CartAttribute } from "@t";
import { updateCartAttributesMutation } from "@gql";
import { safeExtract } from "@utils";

export async function updateCartAttributes(
  fetchShopify: FetchShopify,
  cartId: string,
  attributes: CartAttribute[]
): Promise<ShopifyCart> {
  const response = await fetchShopify(updateCartAttributesMutation, {
    cartId,
    attributes,
  });

  return safeExtract(
    "updateCartAttributes",
    response?.data?.cartAttributesUpdate?.cart,
    {
      cartId,
      attributes,
      response,
    }
  );
}
