import { FetchShopify, ShopifyCart } from "@t";
import { mergeCartsMutation } from "@gql";
import { safeExtract } from "@utils";

export async function mergeCarts(
  fetchShopify: FetchShopify,
  sourceCartId: string,
  destinationCartId: string
): Promise<ShopifyCart> {
  const response = await fetchShopify(mergeCartsMutation, {
    sourceCartId,
    destinationCartId,
  });

  return safeExtract("mergeCarts", response?.data?.cartMerge?.cart, {
    sourceCartId,
    destinationCartId,
    response,
  });
}
