import { FetchShopify, ShopifyCart } from "@t";
import { removeDiscountMutation } from "@gql";
import { safeExtract } from "@utils";

export async function removeDiscount(
  fetchShopify: FetchShopify,
  cartId: string
): Promise<ShopifyCart> {
  const response = await fetchShopify(removeDiscountMutation, { cartId });

  return safeExtract(
    "removeDiscount",
    response?.data?.cartDiscountCodesUpdate?.cart,
    {
      cartId,
      response,
    }
  );
}
