export const mergeCartsMutation = `
  mutation mergeCarts($sourceCartId: ID!, $destinationCartId: ID!) {
    cartMerge(sourceCartId: $sourceCartId, destinationCartId: $destinationCartId) {
      cart {
        id
        checkoutUrl
        cost {
          totalAmount { amount currencyCode }
        }
      }
    }
  }
`;
